#skills-section {
  background: #f8f9fa; /* Light gray for a softer look */
  color: #2c3e50; /* Dark text for contrast and readability */
  padding: 40px;
  text-align: center;
}

#skills-section h2 {
  color: #2c3e50; /* Dark blue for a professional and consistent feel */
  margin-bottom: 2rem;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr)); /* Responsive grid */
  gap: 20px;
  justify-content: center;
}

.skill-card {
  background: #ffffff; /* White background for the cards */
  border-radius: 8px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05); /* Lighter shadow for a softer look */
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center; /* Center align items */
  min-height: 200px; /* Consistent height */
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out; /* Smooth transition for hover effects */
}

.skill-card:hover {
  transform: translateY(-5px); /* Subtle lift effect on hover */
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1); /* Deeper shadow on hover for more depth */
}

.skill-card h3 {
  color: #2c3e50; /* Dark text for headings to maintain consistency */
  margin-bottom: 1rem;
}

.skill-card p {
  font-size: 1rem;
  color: #6c757d; /* Soft gray for readable contrast */
}

.skill-card .fa-icon {
  margin-bottom: 1rem;
  color: #3498db; /* Blue color to add a pop and maintain the theme */
  font-size: 2rem; /* Larger icon size for visual impact */
}

@media (max-width: 768px) {
  .skills-grid {
    grid-template-columns: 1fr; /* Stack cards on smaller screens */
    gap: 15px;
  }

  .skill-card {
    width: auto; /* Adjust width for smaller screens */
  }
}
