/* AboutMe.css */

#about-me.card {
  background: #ffffff; /* Clean white background */
  border-radius: 20px; /* Rounded corners */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Soft shadow for depth */
  display: flex;
  flex-direction:row; /* Stack children vertically */
  align-items: center; /* Center items vertically */
  gap: 1rem; /* Space between elements */
  padding: 2rem; /* Spacious padding */
  max-width: 800px; /* Maximum width of the card */
  margin: 2rem auto; /* Margin for auto centering */
}

.card-avatar {
  width: 350px; /* Avatar size */
  height: 200px; /* Avatar size */
  border-radius: 50%; /* Circle shape */
  background-size:cover; /* Image covers the avatar */
  background-position: right; /* Image centered within the avatar */
  margin-bottom: 1rem; /* Space below the avatar */
}

.card-info {
  text-align: center; /* Center text for consistency */
}

.card-title {
  font-size: 2.5rem; /* Large font size for the name */
  color: #333; /* Dark color for contrast */
  margin-bottom: 0.5rem; /* Margin bottom for the name */
}

.card-subtitle {
  font-size: 1.25rem; /* Font size for the title/subtitle */
  color: #555; /* Slightly lighter color for the subtitle */
  margin-bottom: 1rem; /* Space below the title */
}

.about-me-content p {
  font-size: 1rem; /* Font size for the description */
  color: #666; /* Lighter text color for readability */
  line-height: 1.5; /* Line height for better readability */
  margin-bottom: 1rem; /* Space between paragraphs */
}

.card-social .card-social__list {
  list-style: none; /* Remove list styles */
  padding: 0; /* Remove default padding */
  display:grid; /* Display social links in a row */
  gap: 1rem; 
  justify-content: center; /* Center icons horizontally */
  margin-top: 1rem; /* Space above the social links */
}

.card-social .card-social__item a {
  color: #3498db; /* Color for social icons/links */
  font-size: 1.5rem; /* Size of social icons */
}

.card-social .card-social__item a:hover {
  color: #2c3e50; /* Darker color on hover */
}

.card-social .card-social__item img {
  width: 24px; /* Size of non-FontAwesome icons */
  height: 24px; /* Size of non-FontAwesome icons */
}

/* Responsive adjustments */
@media (max-width: 768px) {
  #about-me.card {
    padding: 1rem; /* Smaller padding on smaller screens */
  }

  .card-avatar {
    width: 120px; /* Smaller avatar size on smaller screens */
    height: 120px; /* Smaller avatar size on smaller screens */
  }

  .card-title {
    font-size: 2rem; /* Smaller font size for the name on mobile */
  }

  .card-subtitle {
    font-size: 1.5rem; /* Smaller font size for the subtitle on mobile */
  }
}
